import type { Metadata } from "next";
import { Geist, Geist_Mono } from "next/font/google";
import "./globals.css";
import Navbar from "./home/Navbar";
import Sidebar from "./home/Sidebar";

const geistSans = Geist({
  variable: "--font-geist-sans",
  subsets: ["latin"],
  display: "swap",
});

const geistMono = Geist_Mono({
  variable: "--font-geist-mono",
  subsets: ["latin"],
  display: "swap",
});

export const metadata: Metadata = {
  title: "Game Zone",
  description: "A gaming platform for 2D multiplayer games",
};

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="en">
      <body className={`${geistSans.variable} ${geistMono.variable} antialiased`}>
        <div className="flex min-h-screen">
          {/* Sidebar (fixed on the left) */}
          <aside className="w-64 fixed left-0 top-0 h-full bg-gray-900 z-0">
            <Sidebar />
          </aside>

          {/* Main content wrapper (pushes content right of sidebar) */}
          <div className="flex-1 flex flex-col ml-64">
            {/* Navbar (fixed on top) */}
            <header className="fixed top-0 left-64 w-[calc(100%-16rem)] z-10 bg-gray-800">
              <Navbar />
            </header>

            {/* Content area (starts below the navbar) */}
            <main className="p-4 mt-16">{children}</main>
          </div>
        </div>
      </body>
    </html>
  );
}
